.app-container {
    width: 300pt;
    height: 400pt;
}

.alertapp {
    width: 300pt;
    height: 400pt;
    background-color: lightyellow;
    font-size: x-large;
}

.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 400pt;
    right: 300pt;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .3);
}

.modal {
    background-color: #fff;
    border-radius: 5pt;
    border-color: black;
    border-style: solid;
    margin: 0 auto;
    top: 50pt;
    height: 80pt;
    width: 180pt;
    padding: 10pt;
    position: relative;
}

.alert-buttons {
    position: absolute;
    display: flex;
    bottom: 0;
    right: 0;
    padding: 5px;

}
.alert-ok{
    margin: 5px;
}
.alert-cancel{
    margin: 5px;
}
.priority-label{
    margin-right: 5px;
}

